import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return {
            title: '快捷回复',
            list: [],
            user: this.$store.state.userInfo.Data,
            pageShow: false
        }
    },
    created(){
        this.pull()
    },
    methods: {
        pull(){
            this.$post('IMAPI/Account/GetOftenUseWordsList',{
                UserId: this.user.User.DoctorId,
                UserType: 2
            }).then(o => {
                this.pageShow = true
                this.list = o
            })
        },
        send(i){
            this.$bridge.callhandler('reply', i.Content)
            this.$bridge.callhandler('Back')
        }
    }
}



